<template>
  <div class="input_outer">
    <div>
      <img class="santa_logo" src="secret_santa.svg" />
      <div v-if="!linkHash">
        <h3>Secret Santa generator</h3>
        <p>Add particitants, generate and share links</p>
        <div class="input_background">
          <a v-on:click="onClickAdd" class="input_add"><i class="fas fa-2x fa-plus-circle"></i></a>
          <div v-on:click="onGenerate" v-if="Object.keys(names).length > 2" class="input_generate">Create</div>
          <input placeholder="Add a participant..." v-model="content" v-on:keyup.enter="onClickAdd" v-on:focus="onFocus" v-bind:class="{ input_focus: dataProvided }" autocomplete="off" autofocus="off" type="text" class="input" />
          <hex-list :done="isGenerated" :names="names" />
        </div>
      </div>
      <div v-else>
        
        <h3>Merry christmas ðŸŽ„ !</h3>
        <div class="inner_text">
          <h3>HoHoho, <span class="name">{{ linkHash.donor }}</span> !</h3>
          <h2>You are the secret santa of <span class="name">{{ linkHash.receiver }}</span></h2>
          <p>I'm sure you'll find a great gift.</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HexList from "./HexList"

let index = 0

function deserialize(b64) {
  if(typeof b64 !== 'string') {
    return null
  }
  let encoded = b64.replace(/-/g, '+').replace(/_/g, '/')
  return JSON.parse(decodeURIComponent(escape(atob(encoded))))
}

export default {
  name: 'HexInput',
  computed: {
    linkHash: () => { 
      let token = location.hash.replace(/^#/,'').split('#')[0]
      if(!token) {
        return null
      }
      try {
        return deserialize(token) 
      } catch(e) {
        console.error(e)
        return null
      }
    }
  },
  data() {
    return {
      dataProvided: false,
      content: "",
      names: {},
      isGenerated: false
    }
  },
  methods: {
    onFocus() {
      this.dataProvided = true
    },
    onGenerate() {
      let urn = Object.assign({}, this.names)

      for (const [key, value] of Object.entries(this.names)) {
        let localUrn = Object.assign({}, urn)
        delete localUrn[key]
        let foundKey = this.getRandomKey(localUrn)
        
        value["receiver"] = localUrn[foundKey].donor
        delete urn[foundKey]
      }
      this.isGenerated = true
    },
    onClickAdd() {
      this.names[index++] = { donor: this.content, receiver: undefined } 
      this.content = ""
      this.isGenerated = false
      
      for (const [, value] of Object.entries(this.names)) {
        delete value['copied']
      }

    },
    getRandomKey(obj) {
      var keys = Object.keys(obj)
      return keys[keys.length * Math.random() << 0]
    }
  },
  components: {
    HexList
  }
}
</script>

