<template>
  <ul>
    <li v-for="(name, index) in names" v-bind:key="index">
      <div class="text">{{ name.donor }}</div>
      <a v-if="!done" v-on:click="onDelete(index)">
        <i class="fas fa-trash"></i>
      </a>
      <span v-else>
        <a  v-if="!name.copied" v-on:click="onLink(index)">
          <i class="fas fa-link"></i>
        </a>
        <a v-else v-on:click="onLink(index)">
          copied!
        </a>
      </span>
    </li>
  </ul>
</template>

<script>

function serialize(obj) {
  return btoa(
    unescape(
      encodeURIComponent(
        JSON.stringify(obj)
      )
    )
  ).replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
}

export default {
  name: 'HexList',
  props: {
    names: Array,
    done: Boolean
  },
  data() {
    return {
    }
  },
  methods: {
    onDelete(index) {
      delete this.names[index]
    },
    onLink(index) {
      let participant = this.names[index]
      let link = location.href + "#" + serialize(participant)
      navigator.clipboard.writeText(link).then(() => {
        participant.copied = true
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

